import React from "react";
import { Link } from "react-router-dom";

function CardItem(props) {
  return (
    <>
      <li className="cards__item">
        {/* <Link className="cards__item__link" to={props.path}> */}
        <a target="_blank" className="cards__item__link" href={props.path}>
          <figure className="cards__item__pic-wrap" data-category={"Promoção"}>
            {/* <a href="//www.google.com"> */}
            {/* aaaa */}
            <img
              className="cards__item__img"
              alt="Travel Image"
              src={props.src}
              style={{ objectFit: "contain" }}
            />
            {/* </a> */}
          </figure>
          <div className="cards__item__info">
            <h5 style={{ textAlign: "center" }} className="cards__item__text">
              {props.text}
            </h5>
            <h5 style={{ textAlign: "center" }}>na primeira compra</h5>

            <h1>FRETE GRÁTIS</h1>
          </div>
        </a>
        {/* </Link> */}
      </li>
    </>
  );
}

export default CardItem;
